.button {
  display: inline-flex;
  flex-wrap: nowrap;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 23px;
  min-width: 200px;
  max-width: 100%;
  height: 48px;
  line-height: 48px;
  background-color: $bg-orange;
  color: white;
  font-size: 15px;
  text-decoration: none;
  transition: all .3s ease-in-out;
  overflow: hidden;

  &__ico {
    margin-right: 15px;
    flex-shrink: 0;
    background-size: cover; }


  &__text {
    display: inline-block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis; }

  &:hover {
    background-color: $bg-orange-hover; }

  &.is-purple {
    background-color: $bg-purple;
    &:hover {
      background-color: $bg-purple-hover; } }

  &.is-blue {
    background-color: $bg-blue;
    &:hover {
      background-color: $bg-blue-hover; } }

  &.is-green {
    background-color: $bg-green;
    &:hover {
      background-color: $bg-green-hover; } }

  &.is-red {
    background-color: $bg-red;
    &:hover {
      background-color: $bg-red-hover; } }

  &.windows {
    .button__ico {
      background-image: url('data:image/png;base64,');
      width: 14px;
      height: 15px; } }

  &.android {
    .button__ico {
      background-image: url('data:image/png;base64,');
      width: 15px;
      height: 16px; } } }
